
.page {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.markdown {
  position: relative;
  margin: 5rem auto;
  max-width: 800px;
}
.markdown .title {
    font-size: 3.6rem;
    color: #975A58;
    margin: 7.2rem 0 5.4rem;
}
.page {
  position: relative;
  -webkit-box-shadow: white 0 0 0 1.2rem, #4c2d2c 0 0 3rem 0;
          box-shadow: white 0 0 0 1.2rem, #4c2d2c 0 0 3rem 0;
  background: white;
}
.md-display {
  font-size: 1.6rem;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  text-align: left;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  color: black;
  border: 2px solid rgba(76, 45, 44, 0.4);
  min-height: 400px;
  padding: 3.6rem 5.4rem;
}
.md-display h1 {
    color: #975A58;
}
.md-display h2 {
    color: #4c2d2c;
}



