
#app header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#app header {
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
}
html, body {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #975A58;
  font-size: 10px;
}
@font-face {
  font-family: Krungthep;
  src: url(/static/font/Krungthep.ttf);
}
a {
  font-family: Krungthep;
  color: #975A58;
  opacity: .4;
  -webkit-transition: all .3s;
  transition: all .3s;
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
}
a:hover {
    opacity: 1;
}
#app {
  -webkit-animation: colorful 2s;
          animation: colorful 2s;
}
#app header {
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 1000;
}
#app main {
    position: relative;
    top: 0;
}
@-webkit-keyframes colorful {
from {
    -webkit-filter: saturate(0);
            filter: saturate(0);
}
to {
    -webkit-filter: saturate(1);
            filter: saturate(1);
}
}
@keyframes colorful {
from {
    -webkit-filter: saturate(0);
            filter: saturate(0);
}
to {
    -webkit-filter: saturate(1);
            filter: saturate(1);
}
}



